import TagSexAge from "../../components/common/tag-sex-age.vue";
export default {
	components:{
		TagSexAge
	},
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu
		};
	},
	methods: {
		guanzhu() {
			this.isguanzhu = true;
			uni.showToast({
				title: '关注成功',
			});
		},
		imgdetail(index){
			uni.previewImage({
				current: index,
			    urls: this.item.morepic,
			    indicator:"number"
			});
		}
	}
}
